@import url('./styles/key_input.scss');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;500&display=swap');
body {
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: rgb(167, 157, 157);
}
.container {
  padding-top: 80px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  
}
h1 {
  font-weight: 400;
  text-align: center;
}
#gameCanvas {
  border-radius: 20px;
  
  
}
.show-colors {
    box-shadow: 0 0 20px rgb(232, 42, 42);
  }
.nav-bar {
  width: 100%;
  height: 40px;
  position: fixed;
  box-shadow: 0 4px 2px -2px;
  top: 0;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  background-color:rgb(189, 153, 153);
  .text {
    padding: 10px;
    font-size: 20px;
    color:rgb(103, 18, 18);
  }
}
.begin-game-btn {
  border: none;
  border-radius: 10px;
  text-align: center;
  width: 200px;
  height: 50px;
  &:hover {
    cursor: pointer;
  }
}
.hide {
  display: none;
}